html {
  box-sizing: border-box;	
	font-size: 0.875em;
}

*, *:before, *:after {
  box-sizing: inherit;
}

/*body, h1, h2, h3, h4, h5, h6, p, ol, ul {
  margin: 0;
  padding: 0;
}
*/
body {
  scroll-behavior: smooth;
}


ol, ul {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
}

a {
	text-decoration: none;
}

